<template>
  <div class="p_bg">
    <!--  导航栏-->
    <NavBar class="p_nav">
      <i slot="left" class="el-icon-back" @click="back"></i>
      <div slot="center">发表文章</div>
    </NavBar>
<!--    主体-->
    <el-card style="margin: 44px 15px 0 15px">
      <el-form :label-position="'top'" label-width="80px" :model="formLabelAlign" ref="addFormRef" :rules="formLabelAlignRules">
        <el-form-item label="标题" prop="title">
          <el-input v-model="formLabelAlign.title"></el-input>
        </el-form-item>
        <el-form-item label="正文内容" prop="content">
<!--          富文本编辑器组件-->
          <quill-editor v-model="formLabelAlign.content"></quill-editor>
        </el-form-item>
        <el-form-item label="分类" prop="classificationIds">
          <el-select v-model="formLabelAlign.classificationIds" multiple placeholder="请选择">
            <el-option
              v-for="item in catelist"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item >
          <el-button type="primary" @click="submit">确定</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import NavBar from "../../components/navbar/NavBar";
export default {
  name: "publishArticle",
  data() {
    return {
      formLabelAlign: {
        title: '',
        content: '',
        classificationIds: []
      },
      formLabelAlignRules:{
        title:[{
          required: true,
          message: '请输入标题名称',
          trigger: 'blur'
        }],
        content:[{
          required: true,
          message: '请输入文章内容',
          trigger: 'blur'
        }],
        classificationIds:[{
          required: true,
          message: '请选择所属分类',
          trigger: 'blur'
        }],
      },
      catelist:[],
    }
  },
  created() {
    this.getClassification()
  },
  methods: {
    getClassification(){
      this.$http.get('classification').then(res => {
       this.catelist = res.data.data
      }).catch(err => err)
    },
    back(){
      this.$router.push('/my')
    },
    submit(){
      this.$refs.addFormRef.validate(valid => {
        if (!valid) return
        this.$http.post('article', this.formLabelAlign).then(res => {
          this.$message.success('已成功发布文章')
          this.back()
          this.$refs.addFormRef.resetFields()
        }).catch(err => {
          console.log(err);
          this.$message.error('发布文章失败')
        })
      })
    }
  },
  components:{
    NavBar
  }
}
</script>

<style scoped>
.p_bg {
  background-color: #fafafa;
  height: 100vh;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.p_nav {
  background-color: #fafafa;
  font-weight: bold;
  font-size: 25px;
  color: black;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;
}

.p_nav i {
  font-size: 30px;
}
</style>
